import { NextIntlClientProvider } from 'next-intl';
import { getRequestConfig } from '../i18n/request'; // Импортируем конфигурацию
import type { Metadata } from 'next';
import { cookies } from 'next/headers';
import { useEffect } from 'react';
import './styles/globals.css';
import sen from '../../public/fonts/sen';
import './styles/reset.css';

// Функция для установки cookie
const setLocaleCookie = (locale: string) => {
  document.cookie = `locale=${locale}; path=/`;  // Устанавливаем cookie для выбранного языка
  window.location.reload();  // Перезагружаем страницу для применения изменений
};

export const metadata: Metadata = {
  title: 'LictuaPortfolio',
  description: 'Website represents portfolio with works, experience, and main stack',
};

export default async function RootLayout({ children }: { children: React.ReactNode }) {
  const { locale, messages } = await getRequestConfig(); // Получаем данные из request.ts

  return (
    <html lang={locale}>
      <body className={`${sen.className}`}>
        <NextIntlClientProvider messages={messages}>
          {children}
          {/* Кнопки для переключения языка */}
          <div>
            <button onClick={() => setLocaleCookie('en')}>English</button>
            <button onClick={() => setLocaleCookie('ru')}>Русский</button>
          </div>
        </NextIntlClientProvider>
      </body>
    </html>
  );
}
